body {
  padding: 0;
  margin: 0;
}

.App {
  text-align: center;
  display: grid;
  place-items: center;
  min-height: 100vh;
  background-color: #202952;
  padding: 0 10px;
}

.AnimatedText_animatedTextContainer__2wYF6 {
  --maskX: 0;
  --maskY: 0;
  position: relative;
  cursor: default;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.AnimatedText_animatedTextContent__2Dq3A {
  color: #000000;
  font-size: 100px;
  font-weight: 600;
  font-family: "Graduate", cursive;
  line-height: 1.1;
  letter-spacing: 5px;
  text-shadow: 5px 3px 8px rgba(0, 0, 0, 0.2);
  margin: 0;
}

.AnimatedText_animatedTextContentClone__35HvU {
  position: absolute;
  left: 0;
  top: 0;
  color: #ffffff;
  transition: all 0.4s ease-out;
  -webkit-clip-path: polygon(
    0 0,
    calc(var(--maskX) * 1% + (var(--maskY) - 50) * 0.4%) 0,
    calc(var(--maskX) * 1% + (var(--maskY) - 50) * -0.4%) 100%,
    0 100%
  );
          clip-path: polygon(
    0 0,
    calc(var(--maskX) * 1% + (var(--maskY) - 50) * 0.4%) 0,
    calc(var(--maskX) * 1% + (var(--maskY) - 50) * -0.4%) 100%,
    0 100%
  );
}

